<script>
import Loader from '../../components/Loader';
import form from '../../mixins/form';
import { success, fail } from '../../utils/toast';
import classes from '../../utils/validations/classes';
import { getClassQuiz, updateClassQuiz } from '../../utils/api/classes';

export default {
	name: 'ClassQuizDetail',
	mixins: [form],
	data() {
		return {
			loading: true,
			itemData: {},
			errors: {},
			option: ''
		};
	},
	components: {
		Loader
	},
	created() {
		this.getClassQuiz();
	},
	methods: {
		getClassQuiz() {
			getClassQuiz(this.$route.params.classId, this.$route.params.quizId)
				.then((response) => {
					if (response.status === 200) {
						this.itemData = response.data.data;
						this.loading = false;
					}
				})
				.catch((error) => {
					this.loading = false;
					return error;
				});
			return true;
		},
		updateClassQuiz() {
			this.itemData.answer = this.option;
			const result = this.itemData;
			if (this.itemData.answer === undefined) {
				delete result.answer;
			}
			['created_at', 'updated_at', 'id', 'online_class_id', 'is_deleted'].forEach((key) => {
				delete result[key];
			});
			this.validate(result, classes.updateQuiz);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			const { classId, quizId } = this.$route.params;

			updateClassQuiz(classId, quizId, result)
				.then((response) => {
					if (response.status === 200) {
						this.loading = false;
						success({
							title: 'Success! Class Quiz has been updated!'
						});
					}
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		}
	}
};
</script>


